.App{
  position: relative;
  width: 100%;
  height:100%;
  background-color: rgb(18,18,204);
  font-size: 25px;
  // img{
  //   width: 100%;
  //   height: 100%;
  // }
  .container{
    width: 100%;
    height: 100vh;
    display: flex;
    justify-content:space-between;
    flex-direction: column;
    align-items: center;
    z-index: 200;
    
  }
  
  
  .headerContainer{
    background-color: rgb(18,18,204);
    width: 100%;
    padding-top: 2.0rem;
    z-index: 31;
    display: flex;
    margin-bottom: 2rem;
    justify-content: center;
    align-items: center;
    
  }
  .logoWrapper{
    width: 35px;
    height: 35px;
    img{
      width: 100%;
        }
  }
  
  body{
    z-index: 300;
    width: 100%;
    height: 100%;
    display: flex;
    justify-content: space-around;
    // align-items: center;
   

  }
  .logoCentrale{
    background-color: rgb(18,18,204);;
    width: 100%;
    height: 80%;
    position: absolute;
    top: 150px;
  
    z-index: 30;
    display: flex;
    justify-content: center;
    align-items: center;
    img{
      object-fit: contain;
      width: 60%;
    }
  }
}

